import React from 'react';
import Navigation from '../Navigation/Navigation';
import { Box,Grid, Container, AppBar, Toolbar, Typography, Button, Card, CardActionArea, CardContent, Divider } from '@material-ui/core';
import Variants from './Variant';
import {SchoolRounded, Home,Person} from '@material-ui/icons'
import profileData from './profileData';

export default function Profile() {
    const [data, setdata] = React.useState([]);
    React.useEffect(() => {
        setdata(profileData);
    }, [])
    return (
        <Box>
            <Box >
                <AppBar position="static">
                    <Toolbar>
                        <Typography variant="h6" style={{ flexGrow: 1 }}>
                            Bookstore(IMJC)
    </Typography>
                        <Button color="inherit">Login</Button></Toolbar>
                </AppBar>
                <Box className="main-content">
                    <Container className="container">
                        <Grid
                        container
                        justify="center"
                        alignContent="center"
                        alignItems="center"
                        className="row">
                            {!data.length ? <Variants /> : (
                                data.map(data =>
                                    <Card key={data.name}>
                                        <CardActionArea>
                                            <img alt={data.name} height={400} style={{textAlign:'center',display:'flex', justifyContent:'center',borderRadius:'10px'}} src={data.img} />
                                            <CardContent>
                                                <Typography style={{textAlign:'center'}} component="h4" variant="h4">
                                                    {data.name}
                                                </Typography>
                                                <Divider/>
                                                <Typography component="small" variant="small" >
                                                    <SchoolRounded/>{data.school}
                                                </Typography><br/>
                                                <Typography component="small" varaint="small">
                                                   <Person/> {data.profession}
                                                </Typography><br/>
                                                <Typography component="small" variant="small">
                                                    <Home/>{data.address}
                                                </Typography>
                                                <Divider/>
                                                <Typography style={{textAlign:'center'}} component="p" variant="p">
                                                    {data.description}
                                                </Typography>
                                            </CardContent>
                                        </CardActionArea>
                                    </Card>
                                )
                            )}
                        </Grid>
                    </Container>
                </Box>
            </Box>
            <Navigation />
        </Box>

    )
}
