import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import HomeIcon from '@material-ui/icons/HomeRounded';
import AddIcon from '@material-ui/icons/AddBoxRounded';
import ProfileIcon from '@material-ui/icons/PersonRounded';
import { Link } from 'react-router-dom';

const useStyles = makeStyles({
  root: {
    width: 500,
    bottom:0
  },
});

export default function Navigation() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  return (
    <div className="nav-botn">
      <BottomNavigation
        value={value}
        onChange={(event, newValue) => {
          setValue(newValue);
        }}
        showLabels
        className={classes.root}
      >
          <BottomNavigationAction value="/" component={Link} to="/" label="Home" icon={<HomeIcon />} />
          <BottomNavigationAction value="/add" component={Link} to="/add" label="Add Books" icon={<AddIcon />} />
          <BottomNavigationAction value="profile" component={Link} to="/profile" label="Profile" icon={<ProfileIcon />} />
      </BottomNavigation>
    </div>
  );
}
