import React,{useState,useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import Navigation from '../Navigation/Navigation'
import { Grid, AppBar, Toolbar } from '@material-ui/core';
import WriteIcon from "@material-ui/icons/EditOutlined";
import PostedIcon from "@material-ui/icons/PersonOutlineTwoTone";
import data from '../data'
import Variants from '../Profile/Variant';
const useStyles = makeStyles({
    root: {
        maxWidth: 345,
        margin: 10
    },
    media: {
        height: 140,
    },
    title: {
        flexGrow: 1,
    }
});


export default function Home() {
    const classes = useStyles();
    const [datas, setdata] = useState([])
    useEffect(() => {
       setdata(data);
       console.log("effect")
    }, [])

    return (
        <Box>

            <Box>
                <AppBar position="static">
                    <Toolbar>
                        <Typography variant="h6" className={classes.title}>
                            Bookstore(IMJC)
    </Typography>
                        <Button color="inherit">Login</Button></Toolbar>
                </AppBar>
                <Grid  className="main-content"
                    container
                    direction="row"
                    justify="center"
                    alignItems="center" >
                        {!data.length?(
<Box>
    <Variants />
</Box>
                        ):(
                            datas.map(data => <Card key={data.id} className={classes.root}>
                                <CardActionArea>
                                    <CardMedia
                                        className={classes.media}
                                        image={data.image}
                                        title={data.name}
                                    />
                                    <CardContent>
                                        <Typography gutterBottom variant="h5" component="h2">
                                            {data.name}
                                        </Typography>
                                        <Typography variant="subtitle2" color="secondary" component="span">
                                            <WriteIcon />  {data.author}
                                        </Typography>
        
                                        <Typography variant="body2" color="textSecondary" component="p">
                                            {data.description}
                                        </Typography>
                                        <Typography variant="subtitle1" color="secondary" component="span">
                                            <PostedIcon /> {data.postedBy}
                                        </Typography>
                                    </CardContent>
                                </CardActionArea>
                                <CardActions>
                                    <Button size="small" color="primary">
                                        Share
                </Button>
                                    <Button size="small" color="primary">
                                        Learn More
                </Button>
                                </CardActions>
                            </Card>)
                        )}


                </Grid>
            </Box>
            < Navigation />
        </Box>
    )
}
